import axios from 'axios'
const baseUrl = '/lists'


let token = null

const setToken = newToken => {
    token = `bearer ${newToken}`
}

const pushToWatchlist = async(userId, productId) => {
    const config = {
        headers: {Authorization: token}
    }

    const response = await axios.put(baseUrl + '/watchlist/' + productId.productId, productId.productId, config)
    return response.data
}

const pushToCollection = async(userId, productId) => {
    const config = {
        headers: {Authorization: token}
    }

    const response = await axios.put(baseUrl + '/collection/' + productId.productId, productId.productId, config)
    return response
}

const deleteFromWatchlist = async(userId, productId) => {
    const config = {
       headers: {Authorization: token}
    }
    
    const response = await axios.put(baseUrl + '/watchlist/delete/' + productId, productId, config)
    return response
}

const deleteFromCollection = async (productId) => {
    const config = {
       headers: {Authorization: token}
    }

    const response = await axios.put(baseUrl + '/collection/delete/' + productId, productId, config)
    return response
}

const increment = async (productId) => {
    const config = {
       headers: {Authorization: token}
    }
 
    const response = await axios.put(baseUrl + '/collection/increment/' + productId, productId, config)
    return response.data
}


const decrement = async (productId) => {
    const config = {
       headers: {Authorization: token}
    }
    
    const response = await axios.put(baseUrl + '/collection/decrement/' + productId, productId, config)
    return response.data
}


export default {
    setToken,
    pushToWatchlist,
    pushToCollection,
    deleteFromWatchlist,
    deleteFromCollection,
    increment,
    decrement
}