import React, {useState, useEffect} from 'react'
import listService from '../services/listService'
import userService from '../services/userService'
import {LOCAL_STORAGE_ID} from '../utils/constants'
import { MdDeleteForever } from 'react-icons/md'
import ProductModal from './ProductModal'
import * as TableUtils from '../utils/table'
import BootstrapTable from 'react-bootstrap-table-next'

const Watchlist = () => {
    const [user, setUser] = useState(null)
    const [watchlist, setWatchlist] = useState([])
    const [show, setShow] = useState(false)
    const [selectedProduct, setSelectedProduct] = useState(null)

    const nameFormatter = (cell, row) => {
        return (
            <button onClick={() => openProductModal(row.product)}>{row.product.name}</button>
        )
    }

    const openProductModal = (product) => {
        setSelectedProduct(product)
        setShow(true)
    }

    const getWatchlist = (userId) => {
        userService
            .getUser(userId)
            .then(response => {
                setWatchlist(response.data.watchlist)
            })
      }

    const deleteProduct = async (productId) => {
        const updatedData = await listService.deleteFromWatchlist(productId)
        setWatchlist(updatedData.watchlist)
    }

  

    const buttonFormatter = (cell, row) => {
        return (
            <div>               
                <button onClick={() => deleteProduct(row.product._id)}>
                    <MdDeleteForever />
                </button>
            </div>
        )
    }

    const columns = [{
        dataField: 'product.name',
        text: 'Name',
        sort: true,
        formatter: nameFormatter
    }, {
        dataField: 'product.group.groupName',
        text: 'Set',
        sort: true
    }, {
        dataField: 'product.cardNumber',
        text: 'Number',
        sort: true
    }, {
        dataField: 'product.cardRarity',
        text: 'Rarity',
        sort: true
    }, {
        dataField: 'product.currentMarketPrice',
        text: 'Unit Price',
        sort: true,
        formatter: TableUtils.priceFormatter
    }, {
        dataField: 'difference',
        text: 'Change',
        isDummyField: true,
        sort: true,
        sortFunc: TableUtils.differenceSort,
        formatter: TableUtils.differenceFormatter
    }, {
        dataField: 'percentDifference',
        text: 'Change (%)',
        isDummyField: true,
        sort: true,
        sortFunc: TableUtils.percentDifferenceSort,
        formatter: TableUtils.percentDifferenceFormatter
    }, {
        dataField: 'quantity',
        text: 'Quantity',
        sort: true,
    }, {
        dataField: 'button',
        isDummyField: true,
        formatter: buttonFormatter
    }]

    useEffect(() => {
        const loggedUserJSON = window.localStorage.getItem(LOCAL_STORAGE_ID)
        if(loggedUserJSON) {
            const user = JSON.parse(loggedUserJSON)
            setUser(user)
            getWatchlist(user.id)
            listService.setToken(user.token)
        }
    }, [])

    const watchlistTable = () => {
        return (
            <div>
                <ProductModal product={selectedProduct} show={show} handleClose={() => setShow(false)} />
                <BootstrapTable bootstrap4={true} keyField='_id' data={watchlist} columns={columns} />
            </div>
        )
    }

    return (
        <div align="center">
            <h1>Collection</h1>
            {user ? watchlistTable() :
                <div>
                    Log in first to see your watchlist! If you just logged in, reload the page.
            </div>}
        </div>
    )
}

export default Watchlist