import axios from 'axios'
const baseUrl = '/users'

const getUser = (userId) => {
  return axios.get(baseUrl + '/' + userId)
}

const registerUser = async (username, password) => {
  const newUser = {
    username: username,
    password: password
  }

  const response = await axios.post(baseUrl, newUser)
  console.log(response)
}

export default {
  getUser,
  registerUser
}
