import React, { useEffect, useState } from 'react'
import listService from '../services/listService'
import userService from '../services/userService'
import { LOCAL_STORAGE_ID } from '../utils/constants'
//import { Button, Col, Form, InputGroup } from 'react-bootstrap'

const RegisterForm = () => {
  const [username, setUsername] = useState("")
  const [password, setPassword] = useState("")
  const [validationMessage, setValidationMessage] = useState("")
  const [registered, setRegistered] = useState(false)

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem(LOCAL_STORAGE_ID)
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      listService.setToken(user.token)
    }
  }, [])

  const handleRegistration = async (event) => {
    event.preventDefault()
    try {
      const user = await userService.registerUser(username, password)
      setRegistered(true)
      //Tell user to log in
    } catch (exception) {
      //Force unique usernames
      //Could be improved I'm sure
      setValidationMessage("Username already in use!")
    }
  }

  const loginForm = () => {
    return (
      <div>
        <h2>New user? Register here:</h2>
        <form onSubmit={handleRegistration}>
          <div>
            Username &nbsp; <input type="text" value={username} onChange={({ target }) => setUsername(target.value)} />
          </div>
          <div>
            Password &nbsp; <input type="text" value={password} name="Password" onChange={({ target }) => setPassword(target.value)} />
          </div>
          <div>
            <p>{validationMessage}</p>
          </div>
          <button type="submit">Register</button>
        </form>
      </div>
    )
  }

  return (
    <div>
      {registered ? <div>Success! Log in with your new credentials.</div> : loginForm()}
    </div>
  )
}

export default RegisterForm