import React from 'react'
import {Button, Modal} from 'react-bootstrap'

const ProductModal = ({show, product, handleClose}) => {  
  
    if(!product) {
      return null
    }
    
    return (
      <>  
        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Card Information</Modal.Title>
          </Modal.Header>
          <Modal.Body>
          <div>
          <table>
            <tbody>
              <tr>
                <td rowSpan="3">
                  <div className="product-image">
                    <img src={product.imageUrl} alt={product.name} />
                  </div>
                </td>
                <td>
                  <div className="product-name">
                    {product.name}
                  </div>
                  <div className="product-info">
                    {product.group ? product.group.groupName : 'Set Name Not Found'}
                  </div>
                  <div className="product-info">
                    {product.cardNumber}
                  </div>
                  <div className="product-info">
                    {product.cardRarity}
                  </div>
                </td>
              </tr>
              <tr>
                <td>
                  <div className="product-info">
                    Market Price: {product.currentMarketPrice}
                  </div>
                </td>
              </tr>
              <tr>
                <td>
                  <a href={product.storeUrl}>TCGPlayer prices</a>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={handleClose}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </>
    )
  }

export default ProductModal  