import React from 'react'

const Privacy = () => {
    return (
        <div className="wrapper">
            <h1>Privacy Policy</h1>
            <p>
                This site in itself does not store any data about you or your computer.
            </p>
            <p>
                This site will store the items you add to your collection and watchlist in its database. This data is not sold or given out to any third parties. If you
                delete your account this data will be automatically removed from the database.
            </p>
        </div>
    )
}

export default Privacy